@charset "UTF-8";
body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
dl,
ol,
ul,
dd,
input,
textarea,
button {
  padding: 0;
  margin: 0; }

img {
  vertical-align: top;
  border: 0;
  width: 100%; }

a {
  color: inherit;
  text-decoration: none; }

li {
  list-style: none; }

body {
  font-family: "微软雅黑", "宋体", Arial;
  font-size: 12px;
  color: #666; }

input {
  color: inherit;
  font-size: inherit;
  font-family: inherit; }

textarea {
  resize: none; }

input:focus,
button:focus,
textarea:focus,
select:focus {
  outline: none; }

/*input,button,textarea,select{
	outline: none;
}*/
input[type="button"],
input[type="reset"],
input[type="submit"] {
  cursor: pointer; }

.sl {
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis; }

.w {
  width: 1200px;
  margin-left: auto;
  margin-right: auto; }

.clear:after {
  content: "";
  display: block;
  clear: both;
  height: 0;
  overflow: hidden;
  visibility: hidden; }

.clear {
  zoom: 1; }

a:hover {
  color: #e62318; }
